  /*REFER A FRIEND*/
  .banner__refer {
    margin-top: 50px !important;
  }
  .refer__pic {
    position: relative !important;
    width: 40% !important;
    height: 20% !important;
    margin-top: 20px !important;
    -webkit-box-align: right !important;
        -ms-flex-align: right !important;
            align-items: right !important;
  }
  .banner__refer__title {
    font-size: 1.125em !important;
  }
  .banner__refer__info h1 {
    font-size: 1.875em !important;
  }
  .banner__refer__info p {
    font-size: 0.625em !important;
    width: 100% !important;
  }
  .banner__refer__info a {
    width: 60% !important;
    height: 40px !important;
    font-size: 0.625em !important;
  }
  /*FOOTER*/
  .footer {
    display: block !important;
  }
  .footer__menu {
    padding: 0 5px 20px 5px !important;
  }
  .footer_menu_title {
    padding: 0 5px 20px 20px !important;
  }
  .footer__menu div {
    padding: 0 5px 20px 5px !important;
    -webkit-column-count: 2 !important;
            column-count: 2 !important;
    -webkit-column-gap: 14px !important;
            column-gap: 14px !important;
  }
  .footer__menu li {
    width: 40% !important;
    padding: 3px !important;
  }
  .footer__menu a {
    color: #474747 !important;
  }
  /*JOIN NEWSLETTER*/
  .newsletter__container {
    -webkit-box-pack: left !important;
        -ms-flex-pack: left !important;
            justify-content: left !important;
    margin-left: 20px !important;
  }
}

